import { SHA256, enc } from 'crypto-js'

/**
 *    SHA-256   
 * @param input 
 * @returns SHA-256   (16 )
 */
export function sha256(
  input: string,
  salt: string = 'EJJxE4eltIGA2c78zxboCQHw'
): string {
  const hash = SHA256(input + salt) // SHA-256  
  return hash.toString(enc.Hex) //  16  
}
